@import "reset";
@import "vars";
@import "mixins";
@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800&display=swap");

html,
body {
  @include antialiasing();
  overflow-x: hidden;
}

body {
  background: $body-bg;
  min-height: 200vh;
  font-family: "Poppins";
}

header {
  width: 100%;
  height: 30vh;
  display: grid;
  place-content: center;
  background: $header-bg;

  h1 {
    text-transform: uppercase;
    color: $header-c;
  }
}

.wrapper {
  width: 100%;
  height: 100%;
  display: grid;
  place-content: center;

  .calculator {
    width: 800px;
    height: 500px;
    background: #000;

    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(7, 1fr);
    grid-column-gap: 3px;
    grid-row-gap: 3px;

    [class*="__item"] {
      border: 1px solid $item-border-c;
      font-size: 50px;
      @include flexCenter(desktop);
      cursor: pointer;

      &:hover {
        background-color: $item-hover-bg;
      }
    }

    .klik {
      @include scale(0.98);
      background-color: #ffa600;
    }

    #display {
      display: flex;
      justify-content: flex-end;
      padding: 5px;
      color: $display-c;

      &:hover {
        background: $item1-bg;
        cursor: auto;
      }
    }
//=========================
//  number buttons styling
//=========================
    &__item12,
    &__item13,
    &__item14,
    &__item17,
    &__item18,
    &__item19,
    &__item22,
    &__item23,
    &__item24,
    &__item27 {
      color: $item-calculator-c;
      background: $item-numbers-bg;
    }

    //===========================
    //  operator buttons styling
    //===========================
  &__item6{
    background: red;
  }

  &__item5{
    background: blue;
  }



   &__item9,
   &__item11,
   &__item15,
   &__item16,
   &__item20,   
   &__item25,   
   &__item26,   
   &__item28,   
   &__item29,  
   &__item31 

   {
     background: orange;
   }


//####################################################
//################# calculator grid   ################
//####################################################

    &__item1 {
      grid-area: 1 / 1 / 2 / 6;
      background: $item1-bg;
    }
    &__item2 {
      grid-area: 2 / 1 / 3 / 2;
    }
    &__item3 {
      grid-area: 2 / 2 / 3 / 3;
    }
    &__item4 {
      grid-area: 2 / 3 / 3 / 4;
    }
    &__item5 {
      grid-area: 2 / 4 / 3 / 5;
    }
    &__item6 {
      grid-area: 2 / 5 / 3 / 6;
    }
    &__item7 {
      grid-area: 3 / 1 / 4 / 2;
    }
    &__item8 {
      grid-area: 3 / 2 / 4 / 3;
    }
    &__item9 {
      grid-area: 3 / 3 / 4 / 4;
    }
    &__item10 {
      grid-area: 3 / 4 / 4 / 5;
    }
    &__item11 {
      grid-area: 3 / 5 / 4 / 6;
    }
    &__item12 {
      grid-area: 4 / 1 / 5 / 2;
    }
    &__item13 {
      grid-area: 4 / 2 / 5 / 3;
    }
    &__item14 {
      grid-area: 4 / 3 / 5 / 4;
    }
    &__item15 {
      grid-area: 4 / 4 / 5 / 5;
    }
    &__item16 {
      grid-area: 4 / 5 / 5 / 6;
    }
    &__item17 {
      grid-area: 5 / 1 / 6 / 2;
    }
    &__item18 {
      grid-area: 5 / 2 / 6 / 3;
    }
    &__item19 {
      grid-area: 5 / 3 / 6 / 4;
    }
    &__item20 {
      grid-area: 5 / 4 / 6 / 5;
    }
    &-item21 {
      grid-area: 5 / 5 / 6 / 6;
    }
    &__item22 {
      grid-area: 6 / 1 / 7 / 2;
    }
    &__item23 {
      grid-area: 6 / 2 / 7 / 3;
    }
    &__item24 {
      grid-area: 6 / 3 / 7 / 4;
    }
    &__item25 {
      grid-area: 6 / 4 / 7 / 5;
    }
    &__item26 {
      grid-area: 6 / 5 / 7 / 6;
    }
    &__item27 {
      grid-area: 7 / 1 / 8 / 3;
    }
    &__item28 {
      grid-area: 7 / 3 / 8 / 4;
    }
    &__item9 {
      grid-area: 7 / 4 / 8 / 5;
    }
    &__item30 {
      grid-area: 7 / 5 / 8 / 6;
    }
    &__item31 {
      grid-area: 7 / 5 / 8 / 6;
    }
  }
}
